.container{
    position: fixed;
    left: -18px;
    top: -18px;
    width: 100vw;
    height: 100vh;
    z-index: 5;
    animation: animMenu .3s 1;
    display: relative;
    align-items: center;
    justify-content: center;
}
.menu{
    text-decoration: none;
    position: absolute;
    left: 42%;
    top: 30%;
    font-family: 'New Rocker';
    font-style: normal;
    font-weight: 400;
    font-size: 70px;
    line-height: 86px;
    text-align: center;
    color: white;
}
.anchor{
    text-decoration: none;
    color: white;
    transition: all .2s;
    cursor: pointer;
}
.anchor:hover{
    opacity: .9;
    color:rgb(200, 184, 169);
}
.background{
    width: 104%;
    height: 104%;
}
.close{
    position: absolute;
    width: 80px;
    right: 25px;
    top: 30px;
    cursor: pointer;
    transition: all .2s;
}
.close:hover{
    transform: scale(1.1);
}

.icons{
    position: absolute;
    display: flex;
    flex-direction: column;
    margin-top: 4%;
    left: 140px;
    bottom: 10px;
}
.icon{
    margin-bottom: 10px;
    width: 52px;
    cursor: pointer;
    transition: all .3s;
}
.icon:hover{
    transform: scale(1.1);
}

@keyframes animMenu {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}